<script setup lang="ts">
import type { Ref } from "vue";
import { useRouteQuery } from "@vueuse/router";
import { DATE_RANGE } from "~/constants";

export interface ISearchFilters {
  sportTypes: Ref<TSportType[]>;
  eventDate: Ref<TDateRange>;
  categories: Ref<string>;
  gender: Ref<TGender | "all">;
}

const { t } = useI18n();
const route = useRoute();
const router = useRouter();
const runtimeConfig = useRuntimeConfig().public;
const componentKey = ref(0); // the component update dynamically if user enter new search value.
const searchValue = useRouteQuery<string>("q", "");
const searchInput = ref();
const searchFilters = reactive({
  sportTypes: useRouteQuery<TSportType[]>("sport_type", [], { route, router }),
  eventDate: useRouteQuery<TDateRange>("event_date", "anytime", { route, router }),
  categories: useRouteQuery<string>("categoryId", "all", { route, router }),
  gender: useRouteQuery<TGender | "all">("gender", "all", { route, router }),
} as ISearchFilters);
const hasGenderFilter = runtimeConfig.APP_CUSTOMER.enable_gender_classification;

const resultState = reactive({
  videos: true,
  categories: true,
});

const noResultsFound = computed(() => {
  return !Object.values(resultState).some(hasItems => hasItems);
});

const canClearFilters = computed(() =>
  Boolean(searchFilters.sportTypes?.length
    || searchFilters.eventDate !== DATE_RANGE.ANYTIME
    || searchFilters.categories !== "all"
    || searchFilters.gender !== "all"),
);

const forceUpdate = () => componentKey.value++;

const clearFilters = () => {
  router.replace({ query: { q: searchValue.value } });
};

const clearSearch = () => {
  searchValue.value = "";
};

watch(() => route.query.event_date, forceUpdate);
watch(() => route.query.gender, forceUpdate);
watch(() => route.query.categories, forceUpdate);
watch(() => route.query.sport_type, (newQuery, oldQuery) => {
  if (JSON.stringify(newQuery) !== JSON.stringify(oldQuery)) // we need compare two arrays of strings.
    forceUpdate();
}, { deep: true });

watch(() => searchValue.value, (newValue, oldValue) => {
  if (newValue === "" && oldValue !== "") {
    forceUpdate();
  }
});

onBeforeMount(() => {
  if (route.query.sport_type && !Array.isArray(route.query.sport_type))
    route.query.sport_type = [route.query.sport_type];
});

onMounted(() => searchInput.value.focus());
useHead({ title: t("labels.search") });
definePageMeta({ layout: "home" });
</script>

<template>
  <div class="main-container">
    <div class="mb-10">
      <div class="flex items-center justify-center my-8">
        <div class="baseInputGroup w-full justify-center max-w-[560px]">
          <BaseSearchInput
            ref="searchInput"
            v-model="searchValue"
            :placeholder="t('labels.search_event_league_team')"
            class="w-full"
            root-class="truncate"
            @keyup.enter="forceUpdate"
            @clear="clearSearch()"
          />
        </div>
      </div>
      <ListFilters
        v-model:sport-types="searchFilters.sportTypes"
        v-model:event-date="searchFilters.eventDate"
        v-model:categories="searchFilters.categories"
        v-model:gender="searchFilters.gender"
        has-event-date-filter
        has-sport-type-filter
        has-category-filter
        :has-gender-filter="hasGenderFilter"
        :has-clear-button="canClearFilters"
        @clear="clearFilters()"
      />
      <div class="hr" />
    </div>

    <SearchVideoResults
      :key="componentKey"
      :search-value="searchValue"
      :date-range="searchFilters.eventDate"
      :main-category-id="searchFilters.categories"
      :sport-type-filter="searchFilters.sportTypes.length ? searchFilters.sportTypes : undefined"
      :gender="searchFilters.gender"
      @no-items="resultState.videos = false"
      @has-items="resultState.videos = true"
    />
    <div class="mt-10">
      <SearchCategoryResults
        :key="componentKey"
        :search-value="searchValue"
        :date-range="searchFilters.eventDate"
        :main-category-id="searchFilters.categories"
        :sport-type-filter="searchFilters.sportTypes.length ? searchFilters.sportTypes : undefined"
        :search-limit="3"
        @no-items="resultState.categories = false"
        @has-items="resultState.categories = true"
      />
    </div>

    <div v-if="noResultsFound" class="h-full flex items-center justify-center">
      <BaseNotFound :text="t('errors.could_not_find_results')" />
    </div>
  </div>
</template>
