<script setup lang="ts">
import useAsync from "~/composables/use-async";
import SearchAPI from "~/services/search";

export interface CategoryResultsProps {
  /**
   * An optional string that represents the main category id of events to display.
   */
  mainCategoryId?: string;
  /**
   * An optional date range object that represents the range of dates for the events to display.
   */
  dateRange?: TDateRange;
  /**
   * An optional array of sport types to filter the events by.
   */
  sportTypeFilter?: TSportType[];
  /**
   * An optional string to search the events by.
   */
  searchValue: string;
  /**
   * An optional gender to filter the category by.
   */
  gender?: TGender | "all";
  /**
   * An optional minimum value of length searchValue. it prevent redundant calls, if string is less than this limit characters.
   */
  searchLimit?: number;
}
const props = withDefaults(defineProps<CategoryResultsProps>(), {
  searchLimit: 0,
});
const emit = defineEmits(["noItems", "hasItems"]);

const onError = () => emit("noItems");
const { data: categories, loading, run: search } = useAsync(SearchAPI.identitySubFacetsForMain, { onError });
const runtimeConfig = useRuntimeConfig().public;
const isLeagueView = runtimeConfig.APP_CUSTOMER.enable_leagues_view;

onMounted(() => {
  if (props.searchValue.length >= props.searchLimit && !isLeagueView) {
    search({
      searchString: props.searchValue,
      identityId: props.mainCategoryId === "all" ? undefined : props.mainCategoryId,
    });
  }

  emit("noItems");
});

watch(categories, () => {
  if (categories.value?.length === 0)
    emit("noItems");
  else
    emit("hasItems");
});
</script>

<template>
  <ListIdentityItems
    v-for="category in categories"
    :key="category.identityId"
    :items="category.relatedIdentities"
    hide-on-no-content
    link-list
    :identity-id="category.identityId"
    has-see-all-link
    see-all-link-placement="bottom-center"
    :loading="loading"
    :see-all-link="`/category/${category.identityId}/`"
    :search-value="props.searchValue"
    class="mt-10"
  >
    <template #start>
      <div class="font-bold text-2xl text-main mb-10">
        {{ category.identityTitle }}
      </div>
    </template>
    <template #end>
      <div class="hr mt-10" role="separator" />
    </template>
  </ListIdentityItems>
</template>
