<script setup lang="ts">
export interface VideoResultsProps {
  /**
   * An optional string that represents the main category id of events to display.
   */
  mainCategoryId: string;
  /**
   * An optional date range object that represents the range of dates for the events to display.
   */
  dateRange: TDateRange;
  /**
   * An optional array of sport types to filter the events by.
   */
  sportTypeFilter?: TSportType[];
  /**
   * An optional gender to filter the events by.
   */
  gender: TGender | "all";
  /**
   * An optional string to search the events by.
   */
  searchValue: string;
}

type TSearchType = "live" | "upcoming" | "latest" | "highlight" ;

interface ISearchStateList {
  live: boolean;
  upcoming: boolean;
  latest: boolean;
  highlight: boolean;
}

const { t } = useI18n();
const emit = defineEmits(["noItems", "hasItems"]);
const props = defineProps<VideoResultsProps>();
const appConfig = useRuntimeConfig().public;
const isGameHighlightsEnabled = appConfig.APP_CUSTOMER.enable_game_highlights;
const listEventsState = reactive({
  live: true,
  upcoming: true,
  latest: true,
  highlight: !!isGameHighlightsEnabled,
}) as ISearchStateList;

const noVideoResults = computed(() => !Object.values(listEventsState).some(hasItems => hasItems));
const updateListEventsState = (key: TSearchType, hasItems: boolean) => {
  listEventsState[key] = hasItems;
};

watch(listEventsState, () => {
  if (noVideoResults.value)
    emit("noItems");
  else
    emit("hasItems");
}, { immediate: true });
</script>

<template>
  <h1 v-if="!noVideoResults" class="text-2xl font-bold text-main">
    {{ t('labels.videos') }}
  </h1>
  <ListEvents
    :title="t('labels.live')"
    class="mt-10 text-secondary"
    type="live"
    hide-on-no-content
    has-see-all-link
    advanced-search
    :search-value="props.searchValue"
    :sport-type-filter="props.sportTypeFilter"
    :main-category-id="props.mainCategoryId"
    :date-range="props.dateRange"
    :gender="props.gender"
    @no-items="updateListEventsState('live', false)"
    @has-items="updateListEventsState('live', true)"
  >
    <template #end>
      <div class="hr mt-10" role="separator" />
    </template>
  </ListEvents>

  <ListEvents
    :title="t('labels.upcoming')"
    type="upcoming"
    hide-on-no-content
    class="mt-10 text-secondary"
    has-see-all-link
    advanced-search
    :search-value="props.searchValue"
    :date-range="props.dateRange"
    :main-category-id="props.mainCategoryId"
    :sport-type-filter="props.sportTypeFilter"
    :gender="props.gender"
    @no-items="updateListEventsState('upcoming', false)"
    @has-items="updateListEventsState('upcoming', true)"
  >
    <template #end>
      <div class="hr mt-10" role="separator" />
    </template>
  </ListEvents>

  <ListEvents
    :title="t('labels.latest')"
    hide-on-no-content
    has-see-all-link
    class="mt-10 text-secondary"
    latest
    advanced-search
    :search-value="props.searchValue"
    :date-range="props.dateRange"
    :main-category-id="props.mainCategoryId"
    :sport-type-filter="props.sportTypeFilter"
    :gender="props.gender"
    @no-items="updateListEventsState('latest', false)"
    @has-items="updateListEventsState('latest', true)"
  >
    <template #end>
      <div class="hr mt-10" role="separator" />
    </template>
  </ListEvents>

  <ListEvents
    v-if="isGameHighlightsEnabled"
    :title="t('labels.highlights', 0)"
    type="highlight"
    hide-on-no-content
    has-see-all-link
    advanced-search
    class="mt-10 text-secondary"
    :search-value="props.searchValue"
    :date-range="props.dateRange"
    :main-category-id="props.mainCategoryId"
    :sport-type-filter="props.sportTypeFilter"
    :gender="props.gender"
    @no-items="updateListEventsState('highlight', false)"
    @has-items="updateListEventsState('highlight', true)"
  >
    <template #end>
      <div class="hr mt-10" role="separator" />
    </template>
  </ListEvents>
</template>
